.github_Stats:hover {
  /* box-shadow: 1px 10px 30px -5px #00f4ff, 1px 0 20px -5px #00f4ff,
    1px 0 20px -5px #00f4ff; */
  box-shadow: 0 0 0px #00f4ff, 0 0 5px #00f4ff, 0 0 20px #00f4ff,
    0 0 60px #00f4ff;
  transition: 0.2s linear;
  z-index: 2;
  cursor: pointer;
}
.github_img:hover {
  transform: scale(1.1);
  transform-origin: 0.2s linear;
}
